import React from 'react'
import { Select, Row, Col } from 'antd';
import { Link } from 'react-router-dom'
import { LANGUAGE } from 'utils/localStorageConfig'
import { useTranslation } from 'react-i18next'
import { setSessionItem, getSessionItem } from '../../Utils/sessionStorage';
import './nav.less'
import SVG from '../SVG';
const { Option } = Select;
const headerLogo = require('assets/header/herderLogo.png')
interface userInfo {
    name: string
}
interface Iprops {
    anchorEvent: (anchorName: string) => void,
    userInfo?: userInfo,
}
function Nav(props: Iprops) {
    const { t, i18n } = useTranslation();
    const defaultLanguage = getSessionItem(LANGUAGE);
    const { anchorEvent } = props;
    return (
        <section>
            <nav>
                <Row align='middle'>
                    <Col xs={12} sm={12} md={16} lg={18} xl={20}>
                        <div className='upload-box'>
                            <div onClick={() => {
                                anchorEvent('contactUs')
                            }}>
                                <img src={headerLogo} alt="logo" className='logoImg' />
                                <span>
                                    {
                                        t('download')
                                    }
                                </span>
                            </div>
                        </div>
                    </Col>
                    <Col xs={12} sm={12} md={8} lg={6} xl={4} className='logo-info'>
                        <div className='switch-language-box'>
                            <SVG xlinkHref='icon-diqiu' className='headerMap' />
                            <Select defaultValue={defaultLanguage} style={{ minWidth: '110px', textAlign: 'center', color: "#fff" }} bordered={false} onChange={(val: string) => {
                                setSessionItem(LANGUAGE, val)
                                i18n.changeLanguage(val)
                            }}>
                                <Option value="zn">{
                                    t('zn')
                                }</Option>
                                <Option value="en">
                                    {
                                        t('en')
                                    }
                                </Option>
                            </Select>
                        </div>
                        <Link className='loginBtn' to="/verify-phone">
                            {
                                t("login")
                            }
                        </Link>
                    </Col>
                </Row>
            </nav>
        </section >
    )
}

export default Nav
